import { useState, useEffect } from 'react'
import './css/Game.css'
import SingleCard from './SingleCard'
import NavBar from "./NavBar";


const cardImages = [
    { "src": "/img/helmet-1.png", matched: false },
    { "src": "/img/potion-1.png", matched: false },
    { "src": "/img/ring-1.png", matched: false },
    { "src": "/img/scroll-1.png", matched: false },
    { "src": "/img/shield-1.png", matched: false },
    { "src": "/img/sword-1.png", matched: false },
]


function Game(props) {

    // console.log(props.winsizhight + "h");
    // console.log(props.winsiz + "w");

    const [cards, setCards] = useState([])
    const [turns, setTurns] = useState(0)
    const [choiceOne, setChoiceOne] = useState(null)
    const [choiceTwo, setChoiceTwo] = useState(null)
    const [disabled, setDisabled] = useState(false)

    // shuffle cards for new game
    const shuffleCards = () => {
        const shuffledCards = [...cardImages, ...cardImages]
            .sort(() => Math.random() - 0.5)
            .map(card => ({ ...card, id: Math.random() }))

        setChoiceOne(null)
        setChoiceTwo(null)
        setCards(shuffledCards)
        setTurns(0)
    }

    // handle a choice
    const handleChoice = (card) => {
        // console.log(card)
        choiceOne ? setChoiceTwo(card) : setChoiceOne(card)
    }

    // compare 2 selected cards
    useEffect(() => {
        if (choiceOne && choiceTwo) {
            setDisabled(true)

            if (choiceOne.src === choiceTwo.src) {
                setCards(prevCards => {
                    return prevCards.map(card => {
                        if (card.src === choiceOne.src) {
                            return { ...card, matched: true }
                        } else {
                            return card
                        }
                    })
                })
                resetTurn()
            } else {
                setTimeout(() => resetTurn(), 1000)
            }

        }
    }, [choiceOne, choiceTwo])

    // reset choices & increase turn
    const resetTurn = () => {
        setChoiceOne(null)
        setChoiceTwo(null)
        setTurns(prevTurns => prevTurns + 1)
        setDisabled(false)
    }

    // start new game automagically
    useEffect(() => {
        shuffleCards()
    }, [])

    return (
        // <div class="container">
        <>
            <NavBar />
            <div className="Game text-center" style={{ maxWidth: ((4 * (props.winsizhight / 5)) + 60).toString() + "px" }}>
                {/* <h1>Magic Match</h1> */}
                <button className="btnhover" onClick={shuffleCards}>New Game</button>

                <div className="zard-grid">
                    {cards.map(card => (
                        <SingleCard
                            key={card.id}
                            card={card}
                            handleChoice={handleChoice}
                            flipped={card === choiceOne || card === choiceTwo || card.matched}
                            disabled={disabled}
                            winsizhight={props.winsizhight}
                            winsiz={props.winsiz}
                        />
                    ))}
                </div>

                <p className="turnsiz">Turns : <span style={{ color: "#0d6efd" }}>{turns}</span></p>
            </div>
        </>
        // </div>
    );
}

export default Game
